body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}



body {
    margin: 0 auto;
    padding: 30px;
    max-width: 1200px;
  }

.nav-pills a {
    display: inline-block;
    background-color: rgb(255, 255, 255);
    padding: .5em;
    margin-right: 5px;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
  }

.headline {
    border: 2px dotted #444;
    margin-bottom: 5px;
    padding: 5px;
    position: relative;
    background: linear-gradient(to bottom, #eee, rgb(173, 190, 189));
  }
  
  .headline button {
    float: right;
    background: rgb(159, 233, 165);
  }
.results {
    padding: 5px;
    background-color: #fff;
    min-height: 100%;
  }
